<template>
  <div class>
    <LunBo :dataimg="dataimg"></LunBo>
    <div class="Video" v-for="(item, index) in videoClassify" :key="index">
      <div class="top">
        <span class="text1 darkest-text">{{ item.title }}</span>
        <a
          href="javascript:void(0)"
          class="text2 theme-text"
          v-on:click.stop="lookMore(item, index)"
          >查看更多</a
        >
        <!-- <a href="javascript:void(0)" class="text2" @click="lookBack(item,index)" v-else>返回</a> -->
      </div>

      <div class="video-list lightest-bg">
        <div
          class="myVideo"
          v-for="(index, i) in (item.showList || '').slice(0, 6)"
          :key="i"
        >
          <img
            :src="index.images ? index.images.appdetailsmall : defaultPcBackImg"
            class="title"
          />
          <!-- <img src="../../assets/img/back.png" class="title" v-else /> -->
          <a href="javascript:void(0)" @click="goVideo(index, item.showList)">
            <span
              class="iconfont icon-bofang lightest-text"
              id="play-picture"
            ></span>
          </a>
          <p class="text-1 dark-text">{{ index.title }}</p>
          <p class="text-2 light-text">{{ index.categoryTitle }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import videotemplate from "../../components/video-template";
import LunBo from "../../components/lunbo";
import advertisement from "../../components/advertising-tab";
// import videoList from "../../pages/video/components/videoList";

export default {
  data() {
    return {
      videoList: "",
      videoClassify: "",
      dataimg: [
        {
          src: require("../../assets/img/lbvideo1.png")
        },
        {
          src: require("../../assets/img/lbvideo3.png")
        },
        {
          src: require("../../assets/img/lbvideo2.png")
        }
      ]
    };
  },

  mounted() {
    var _this = this;
    var url = this.APIURL + "/classic/video/category";
    this.$axios.post(url).then(function({ data }) {
      _this.videoClassify = data.data;
      // _this.videoList=data.data

      _this.videoClassify.map(val => {
        _this.getData(val).then(data => {
          // console.log(data)
          val.showList = data;
          // _this.videoList=val
          // console.log( _this.videoList)
          val = Object.assign({}, val);
          _this.videoClassify = [].concat(_this.videoClassify);
        });
        return val;
      });
    });
  },
  methods: {
    async getData(val) {
      let dataList = [];
      var url = this.APIURL + "/classic/video/all";
      try {
        let res = await this.$axios.post(url, {
          category: val.id,
          pageSize: 500
        });
        dataList = res.data.data.dataSet;
      } catch (err) {
        console.log(err);
      }
      return dataList;
    },
    //点击跳转
    goVideo(index, showList) {
      var _this = this;
      _this.videoList = showList;
      this.$router.push({
        path: "/video/details"
      });
      // 面包屑需要
      sessionStorage.setItem("ClassicLabel", index.categoryTitle);
      this.$store.commit("setVideo", index);
      this.$store.commit("setVideoList", this.videoList);
      document.title = index.categoryTitle + "_" + "李罕诵经";
    },
    //点击查看更多
    lookMore(item, index) {
      sessionStorage.setItem("ClassicLabel", item.title);
      this.$router.push("/video/videomore");
      this.$store.commit("setVideoList", item.showList);
      document.title = showList.title + "_" + "李罕诵经";
    }
  },
  computed: {},

  watch: {},
  components: {
    LunBo,
    advertisement,
    videotemplate
  }
};
</script>
<style lang="scss"></style>
