<template>
  <div class="ntx3">
    <div v-for="(item,index) in (videoList || '').slice(2,6)" :key="index">
      <a href="javascript:void(0)" @click="goAudioDetail(item,index)">
        <span class="dark-text">{{item.title}}</span>
        <p class="light-text">{{item.categoryTitle}}</p>
      </a>
    </div>
  </div>
</template>
<script>
export default {
  props: ["videoList"],
  data() {
    return {};
  },
  methods: {
    goAudioDetail(item, index) {
      console.log(item, index);

      this.$router.push({
        path: "/video/details"
      });
      this.$store.commit("setVideo", item);
      this.$store.commit("setVideoList", this.videoList);
    }
  }
};
</script>
<style lang="scss">
.ntx3 {
  margin-left: 45px;
  float: left;
  width: 360px;
  height: 255px;
  div {
    width: 100%;
    height: 41px;
    opacity: 1;
    margin: 8px 0 25px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    a {
      text-decoration: none;
    }
    span:nth-child(1) {
      opacity: 1;
      font-size: 16px;
      font-family: Adobe Heiti Std;
      line-height: 24px;
      letter-spacing: 0px;
      margin-left: 10px;
      margin-left: 18px;
    }
    p {
      margin: 0;
      opacity: 1;
      font-size: 14px;
      font-family: Adobe Heiti Std;
      line-height: 20px;
      letter-spacing: 0px;
      margin-left: 18px;
    }
  }
}
</style>
